import ReactToggle from 'react-toggle'
import "react-toggle/style.css"
import './toggle.less'

export default function Toggle({label,className,checked,onClick}){
  
  const click = (e) => {
    onClick && onClick(!checked)
  }
  return (
    <div className='l-toggle'>
      <span className='label-text'>{label}</span>
      <ReactToggle onClick={click}
        checked={checked}
        className={className}
        icons={false} />      
    </div>
  )
}