import React ,{useState,useEffect} from 'react'
import styled from 'styled-components'
import classNames from 'classnames'

const CheckboxContainer = styled.div`
  display: inline-block;
  vertical-align: middle;
`

const Icon = styled.svg`
  fill: none;
  stroke: white;
  stroke-width: 2px;
`
// Hide checkbox visually but remain accessible to screen readers.
// Source: https://polished.js.org/docs/#hidevisually
const HiddenCheckbox = styled.input.attrs({ type: 'checkbox' })`
  border: 0;
  clip: rect(0 0 0 0);
  clippath: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  white-space: nowrap;
  width: 1px;
`

const StyledCheckbox = styled.div`
  background: ${props => (props.checked ? '#5577FD' : 'none')};
  border: 2px solid #5577FD;
  display: inline-block;
  width: 18px;
  height: 18px;
  border-radius: 4px;
  box-sizing: border-box;
  border-radius: 4px;
  cursor : pointer;

  ${HiddenCheckbox}:focus + & {
    box-shadow: 0 0 0 3px pink;
  }

  ${Icon} {
    visibility: ${props => (props.checked ? 'visible' : 'hidden')};
    display: flex;
  }
`


const Checkbox = ({ className,onChange,checked , ...props }) => {
  const [selected, setSelected] = useState(checked)
  const onClick = () => {
    const value = !selected
    setSelected(value)
    onChange && onChange(value);
  }
  useEffect(() => {
    
  }, [checked])
  const clazz = classNames('c-checkbox',{checked : selected})
  return (
    <CheckboxContainer className={className}>
    <HiddenCheckbox checked={selected} {...props}  />
    <StyledCheckbox checked={selected} className={clazz} onClick={onClick}>
      <Icon className='icon' viewBox="0 0 24 24">
        <polyline points="20 6 9 17 4 12" />
      </Icon>
    </StyledCheckbox>
  </CheckboxContainer>
  )}

export default Checkbox
